import React, { Component } from "react";
import {
	Route,
	NavLink,
	BrowserRouter
} from "react-router-dom";
import Home from "./pages/home";
import Seed from "./pages/seed";
import About from "./pages/about";
import Information from "./pages/information";
import Team from "./pages/team";
import Partners from "./pages/partners";
import Projects from "./pages/projects";
import { slide as Menu } from 'react-burger-menu';
import { Scrollbars } from 'react-custom-scrollbars';

class Main extends Component {
	render() {
		return (
			<BrowserRouter>
				<Scrollbars>
					<div className="main">
						<div id="outer-container">
							<Menu right width={ '200px' } >
								<ul className="nav-mobile">
									<li><a onClick={()=>this.setState({currentPage:'Home'})} >Головна</a></li>
									<li><a onClick={()=>this.setState({currentPage:'Seed'})} >Насіння</a></li>
									<li><a onClick={()=>this.setState({currentPage:'About'})} >Про нас</a></li>
									<li><a onClick={()=>this.setState({currentPage:'Information'})} >Інформація</a></li>
									<li><a onClick={()=>this.setState({currentPage:'Team'})} >Команда</a></li>
									<li><a onClick={()=>this.setState({currentPage:'Partners'})} >Партнери</a></li>
									<li><a onClick={()=>this.setState({currentPage:'Projects'})} >Проекти</a></li>
								</ul>
							</Menu>
						</div>

						<div className="top-bar">
							<ul className="menu">
								<li className="menu-item"><NavLink to="/" strict exact>Головна</NavLink></li>
								<li className="menu-item"><NavLink to="/seed">Насіння</NavLink></li>
								<li className="menu-item"><NavLink to="/about">Про нас</NavLink></li>
								<li className="menu-item"><NavLink to="/information">Інформація</NavLink></li>
								<li className="menu-item"><NavLink to="/team">Команда</NavLink></li>
								<li className="menu-item"><NavLink to="/partners/distributors">Партнери</NavLink></li>
								<li className="menu-item"><NavLink to="/projects">Проекти</NavLink></li>
							</ul>
						</div>

						<Route exact path="/" component={Home}/>
						<Route path="/seed" component={Seed}/>
						<Route path="/about" component={About}/>
						<Route path="/information" component={Information}/>
						<Route path="/team" component={Team}/>
						<Route path="/partners" component={Partners}/>
						<Route path="/projects" component={Projects}/>

					</div>
				</Scrollbars>
			</BrowserRouter>
		);
	}
}

export default Main;


